<template>
  <div class="theader">
    <img class="theader-logo" src="https://i.gtja.com/evercos/assets/themes/1.2.1/images/app/logo.svg" alt="">
    <ul class="theader-menu-ul">
      <router-link class="theader-menu-li" to="/homepage" tag="li">首页</router-link>
      <router-link class="theader-menu-li" to="/hangqing" tag="li">自选行情</router-link>
      <router-link class="theader-menu-li" to="/news" tag="li">市场资讯</router-link>
      <router-link class="theader-menu-li" to="/stocks" tag="li">智能选股</router-link>
      <li class="theader-menu-li"><inputsearch></inputsearch></li>
    </ul>


  </div>
</template>

<script>
import inputsearch from '@/modules/InputSearch'
export default {
  components: {
    inputsearch
  }
}
</script>

<style lang="less" scoped>
  .theader {
    position: relative;
    height: 63px;
    padding-left: 220px;
    border-bottom: 1px solid #004e89;
    box-shadow: 0 1px 5px 0px #a7a7a7;
  }
  .theader-logo {
    position: absolute;
    left: 0;
    top: 0;
    width: auto;
    height: 63px;
  }
  .theader-menu-ul {
    display: inline-block;
    line-height: 63px;
  }
  .theader-menu-li {
    display: inline-block;
    margin-right: 12px;
    cursor: pointer;
  }
  .router-link-active {
    color: #004e89;
  }
</style>




