import Vue from 'vue'
import Router from 'vue-router'
import Homepage from '@/pages/Homepage'
import Hangqing from '@/pages/Hangqing'
import Marketnew from '@/pages/Marketnews'
import Stockchoose from '@/pages/Stockchoose'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      redirect: '/homepage'
    },
    {
      path: '/homepage',
      name: 'home',
      component: Homepage
    },
    {
      path: '/hangqing',
      name: 'hangqing',
      component: Hangqing
    },
    {
      path: '/news',
      name: 'new',
      component: Marketnew
    },
    {
      path: '/stocks',
      name: 'stocks',
      component: Stockchoose
    }
  ]
})



