 * @Author: woden0415.wangdong
 * @Date: 2017-08-24 11:46:34
 * @Description: 输入框搜索
*/

<template>
  <div class="search">
    <input class="search-input" v-model="input" placeholder="请输入内容">
    <i class="el-icon-search search-icon"></i>
  </div>
</template>

<script>
export default {
  data () {
    return {
      input: ''
    }
  }
}
</script>

<style lang="less" scoped>
  .search {
    position: relative;
  }
  .search-input {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background-color: #fff;
    background-image: none;
    border-radius: 4px;
    border: 1px solid #bfcbd9;
    box-sizing: border-box;
    color: #1f2d3d;
    display: inline-block;
    font-size: inherit;
    height: 36px;
    line-height: 1;
    outline: none;
    padding: 3px 10px;
    transition: border-color .2s cubic-bezier(.645,.045,.355,1);
    width: 100%;
    &:focus {
      outline: none;
      border-color: #20a0ff;
      + .search-icon {
        color: #20a0ff;
      }
    }
  }
  .search-icon {
    position: absolute;
    top: 41%;
    right: 8%;
    color: #97a8be;
  }
</style>



